.flicker {

}
.flicker-enter {
  animation-name: flicker, shake;
  animation-duration: .5s, .7s;
  animation-timing-function: linear, ease-in-out;
}
.flicker-enter-active {

}
.flicker-leave {
  animation-name: flicker, shake;
  animation-duration: .5s, .7s;
  animation-timing-function: linear, ease-in-out;
}
.flicker-leave-active {
  
}

@keyframes flicker {
  from {
    opacity: 0.5;
    transform: rotateY(0)
  }
  4% {
    opacity: .5;
  }
  6% {
    opacity: .45;
  }
  8% {
    opacity: .55;
  }
  10% {
    opacity: .45;
  }
  11% {
    opacity: .5;
  }
  12% {
    opacity: .65;
  }
  14% {
    opacity: .55;
  }
  16% {
    opacity: .70;
  }
  17% {
    opacity: .55;
  }
  19% {
    opacity: .5;
  }
  20% {
    opacity: .5;
  }
  24% {
    opacity: .5;
  }
  26% {
    opacity: .69;
  }
  28% {
    opacity: .5;
  }
  38% {
    opacity: .45;
  }
  40% {
    opacity: .5;
  }
  42% {
    opacity: .6;
  }
  44% {
    opacity: .5;
  }
  46% {
    opacity: .5;
  }
  56% {
    opacity: .5;
  }
  58% {
    opacity: .75;
  }
  60% {
    opacity: .5;
  }
  68% {
    opacity: .5;
  }
  70% {
    opacity: .6;
  }
  72% {
    opacity: .5;
  }
  93% {
    opacity: .5;
  }
  95% {
    opacity: .6;
  }
  97% {
    opacity: .5;
  }
  to {
    opacity: .5;
    transform: rotateY(360deg);
  }
}

@keyframes shake {
  from, 90% {
    transform: translateX(-1px);
  }
  20%, 80% {
    transform: translateY(2px);
  }
  30%, 50% {
    transform: translateX(-4px);
  }
  40%, 60% {
    transform: translateX(4px);
  }
}

.fade {

}
.fade-enter {
  opacity: 0;
  transform: scale(.5);
}
.fade-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: .3s;
}
.fade-leave {
  opacity: 1;

}
.fade-leave-active {
  opacity: 0;
  transform: scale(.5);
  transition: .3s;
}
/* animation: animationOut .9s; forwards */
.animationInOut {

}
.animationInOut-enter {
  animation: animationIn .9s;
}
.animationInOut-leave {
  animation: animationOut .9s;
}
@keyframes animationIn {
  0% {
    transform: translateY(-3000px);
  }
  60% {
    transform: translateY(30px);
  }
  75% {
    transform: translateY(-15px);
  } 
  90% {
    transform: translateY(5px);
  }
}
@keyframes animationOut {
  0% {
    transform: translateY(5px);
  }
  60% {
    transform: translateY(-15px);
  }
  75% {
    transform: translateY(30px);
  }
  100% {
    transform: translateY(-3000px);
  }
}

.like {
  cursor: pointer;
  width: 50px;
  height: 50px;
  background-image: url('../images/heart.png');
  background-size: 1450px 50px;
  bottom: -100px;
  position: fixed;
}
.like.is-liked {
  animation-name: like, move;
  animation-duration: .8s, 5s;
  animation-timing-function: steps(28), linear;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite, 1;
}
@keyframes like {
  from {
    background-position: 0 0;
  }
  to {
    /* background-position: -2800px 0; */
    background-position: right;
  }
}
@keyframes move {
  from {
    position: absolute;
    bottom: 0;
  }
  to {
    position: absolute;
    bottom: 110%;
  }
}