body {
  font-family: 'Allerta', sans-serif;
  margin: 0;
}


table {
  width: 100%;
}

th {
  background: #273b47;
  color: white;
}

th {
  padding: 10px 20px;
  text-align: left;
}

td {
  padding: 20px;
}

table, td, th {
  border: 1px solid #273b47;
  border-collapse: collapse;
}

input, textarea {
  outline: 0;
}

input:focus, textarea:focus {
  background: lightgray;
}

.contenedor {
  max-width: 800px;
  margin: auto;
  position: relative;
}

.background {
  background-color: #466173;
  background-image: url(/Invie-animated/static/media/background.e9b52209.png);
  background-size: cover;
}

.portada.background.is-animated {
  background: url(/Invie-animated/static/media/team-platzi-azul.70297162.jpg);
  background-repeat: no-repeat;
  -webkit-animation: background 15s linear infinite alternate;
          animation: background 15s linear infinite alternate;
}

@-webkit-keyframes background {
  from {
    background-size: 200%;
    background-position: center;
  }
  20% {
    background-position: right top;
  }
  40% {
    background-position: center;
    background-size: 100%;
  }
  60% {
    background-position: left top;
  }
  80% {
    background-position: left -100px;
  }
  100% {
    background-position: right -100px;
    background-size: 200%;
  }
}

@keyframes background {
  from {
    background-size: 200%;
    background-position: center;
  }
  20% {
    background-position: right top;
  }
  40% {
    background-position: center;
    background-size: 100%;
  }
  60% {
    background-position: left top;
  }
  80% {
    background-position: left -100px;
  }
  100% {
    background-position: right -100px;
    background-size: 200%;
  }
}

