.guitarra {
  margin: 10px 0 40px;
  padding: 10px 2em;
  /*overflow: hidden;*/
  border: 1px solid #1F313C;
  border-radius: 3px;

  display: flex;
  align-items: center;
  min-height: 250px;
  -webkit-transition: .3s;
  transition: .3s;
  position: relative;
}
.guitarra:hover {
  /* box-shadow: x y % color */
  box-shadow: 10px 10px 0 #ff3149;
}
.guitarra-name {
  font-size: 3em;
  margin: 0 0 .2em;
  padding-bottom: .2em;
  border-bottom: 3px dashed #1e313d;
}
/* .guitarra:nth-of-type(1) { */
/* .guitarra:nth-of-type(odd) { */
.guitarra:nth-last-of-type(odd):hover {
  box-shadow: -10px 10px 0 black;
}
.guitarra:nth-of-type(even) {
  justify-content: flex-end;
}

.guitarra:nth-of-type(even) .guitarra-image {
  left: 0;
  right: auto;
  right: initial;
}

.guitarra-image {
  position: absolute;
  right: 0;
  bottom: 0;
}
.guitarra ol {
  padding: 0;
  list-style: none;
  counter-reset: features;
}
.guitarra li::before {
  counter-increment: features;
  content: counter(features) ". ";
}

