@media screen and (max-width: 500px) {
  /* portada */
  .portada .button {
    display: block;
  }
  .precios .header .logotipo {
    margin: 50px 0;
  }
  .burguer-button {
    display: block;
  }
  .portada .contenedor {
    position: static;
  }
  .header {
    position: static;
  }
  .header .contenedor {
    position: static;
  }
  .menu {
    background: rgba(255, 60, 88, .8);
    position: fixed;
    left: -100%;
    top: 0;
    z-index: 2;
    bottom: 0;
    right: 0;
    display: flex;
    width: 100%;
    align-items: center;
    -webkit-transition: .3s;
    transition: .3s;
  }
  .menu.active {
    left: 0;
  }

  .menu ul {
    flex: 1 1;
  }
  .menu li {
    display: block;
    margin: 0;
    padding: 8px 0;
    border-bottom: 1px solid white;
  }

  .menu li:before {
    display: none;
  }

  .menu li:first-child {
    border-top: 1px solid white;
  }
  .portada {
    text-align: center;
  }
  .titulo {
    font-size: 30px;
  }

  .title {
    font-size: 16px
  }
