.fade {

}
.fade-enter {
  opacity: 0;
  -webkit-transform: scale(.5);
          transform: scale(.5);
}
.fade-enter-active {
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: .3s;
  transition: .3s;
}
.fade-leave {
  opacity: 1;

}
.fade-leave-active {
  opacity: 0;
  -webkit-transform: scale(.5);
          transform: scale(.5);
  -webkit-transition: .3s;
  transition: .3s;
}
