.like {
  cursor: pointer;
  width: 50px;
  height: 50px;
  background-image: url(/Invie-animated/static/media/heart.313656d4.png);
  background-size: 1450px 50px;
  bottom: -100px;
  position: fixed;
}
.like.is-liked {
  -webkit-animation-name: like, move;
          animation-name: like, move;
  -webkit-animation-duration: .8s, 5s;
          animation-duration: .8s, 5s;
  -webkit-animation-timing-function: steps(28), linear;
          animation-timing-function: steps(28), linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: infinite, 1;
          animation-iteration-count: infinite, 1;
}
@-webkit-keyframes like {
  from {
    background-position: 0 0;
  }
  to {
    /* background-position: -2800px 0; */
    background-position: right;
  }
}
@keyframes like {
  from {
    background-position: 0 0;
  }
  to {
    /* background-position: -2800px 0; */
    background-position: right;
  }
}
@-webkit-keyframes move {
  from {
    position: absolute;
    bottom: 0;
  }
  to {
    position: absolute;
    bottom: 110%;
  }
}
@keyframes move {
  from {
    position: absolute;
    bottom: 0;
  }
  to {
    position: absolute;
    bottom: 110%;
  }
}
